.search {
  position: relative;

  &__controls {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
  }

  &__button {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    margin: 0 10px 0 0;
    padding: 0;

    &:focus,
    &:hover {
      background-color: transparent;
    }

    &:last-of-type {
      margin: 0;
    }

    &--search {
      width: 16px;
      height: 16px;

      img {
        width: inherit;
        height: inherit;
        opacity: 0.5;
      }
    }
  }
}
